import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CurrentSessionService } from 'src/app/services/current-session.service';
import { ModalServiceService } from 'src/app/services/modal-service.service';

@Component({
  selector: 'btn-finish',
  templateUrl: './finish.component.html',
  styleUrls: ['./finish.component.scss'],
})
export class FinishComponent implements OnInit {
  eventClick: any;
  constructor(
    private modal: ModalServiceService,
    private session: CurrentSessionService,
    private router: Router
  ) {
    this.eventClick = () => {
      this.session.isTesting = false;
      this.router.navigate(['result']);
    };
  }

  ngOnInit(): void {}

  showPopup() {
    this.modal.show(this.eventClick);
  }
}
