import { Injectable } from '@angular/core';
import { CurrentSessionService } from './current-session.service';

@Injectable({
  providedIn: 'root'
})
export class ModalServiceService {

  modalContent:string=''
  headerText:string=''
  primaryBtn:string='Continue'
  modalType:string=''
  private accept:any;
  private isShow:boolean = false
  constructor(private session:CurrentSessionService) {
    // this.session.testing$.subscribe(value=>{ if (value) {
    //   console.log(value);

    // }})
  }

  close(){
    this.isShow = false
  }

  show(_acceptEvent:any){
    this.isShow = true
    if(_acceptEvent){
      this.accept = _acceptEvent
    }
  }

  clickAccept(){
    this.accept()
    this.isShow = false
  }

  get IsShow(): boolean {
    return this.isShow;
  }
}
