import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { Exam, Sheet } from '../model/ToeicTest';

@Injectable({
  providedIn: 'root',
})
export class CurrentSessionService {
   currentBook: Exam = null;
   currentSheetNbr: number = 1;
   currentPart: number = 5;
   isTesting: boolean = false;
   pageType:number=0; //0:not set, 1:first page, 2: last page
   loadBook: Subject<Exam>;
   reLoadPage: Subject<Object>;
   reLoadPageObs;
   remainQuestions:string =''
   remainSeconds: number = 7200;
   testing:Subject<any>;
   testing$;
  constructor() {
    this.loadBook = new Subject<Exam>();

    this.loadBook.subscribe((value) => {
      this.currentBook = value;
    });
    this.reLoadPage = new Subject();
    this.reLoadPageObs = this.reLoadPage.asObservable()

    // this.testing$ = this.testing.asObservable()

  }
}
